 
 .CubePlayer {
  font-family: 'Open Sans', sans-serif;
  background: #4cb892;

	--box-width: 100vw;
}

@import url('https://fonts.googleapis.com/css?family=Open+Sans:300,400,600,700');
 .shadow_right {
	 width: 90px;
	 height: 380px;
	 transition: all 300ms 200ms;
	 top: 0px;
	 position: absolute;
	 pointer-events: none;
	 box-shadow: 0px 0px 0px #303f45 inset;
}
 .MuiSvgIcon-root {
	 transition: all 0.3s;
	 cursor: pointer;
}
 .MuiSvgIcon-root:hover {
	 color: #44f5b7;
}
 .shadow_left {
	 width: 90px;
	 height: 380px;
	 transition: all 300ms 200ms;
	 top: 0px;
	 position: absolute;
	 right: 0;
	 pointer-events: none;
	 box-shadow: 0px 0px 0px #303f45 inset;
}
 .r_trim {
	 width: 260px;
	 height: 660px;
	 position: absolute;
	 left: var(--box-width);
	 z-index: 2;
	 right: 0;
	 top: -82px;
	 margin: auto;
	 background: #4cb892;
}
 .l_trim {
	 width: 260px;
	 height: 660px;
	 position: absolute;
	 z-index: 2;
	 left: -640px;
	 right: 0;
	 top: -33px;
	 margin: auto;
	 background: #4cb892;
}
 .CubePlayer .player_inner, .CubePlayer .player_inner__middle .cube {
	 position: absolute;
	 top: 50%;
	 transform: translateY(-50%);
	 left: 0;
	 right: 0;
	 margin: auto;
}
 .CubePlayer .player_inner__middle .cube_inner > div {
	 width: var(--box-width);
	 height: 380px;
	 background: pink;
	 position: absolute;
	 transform-style: preserve-3D;
}
 .CubePlayer .player_inner {
	 background: #3a4e47;
	 width: var(--box-width);
	 height: 100vh;
	 /* border-radius: 6px; */
	 perspective: 800px;
}
 .CubePlayer .player_inner__top {
	 height: 70px;
	 padding: 24px;
	 background: url('https://s3-us-west-2.amazonaws.com/s.cdpn.io/217233/koan.png');
	 background-size: 103% !important;
	 margin-bottom: 0px;
	 /* border-top-left-radius: 10px;
	 border-top-right-radius: 10px; */
}
 .CubePlayer .player_inner__top .MuiSvgIcon-root, .CubePlayer .player_inner__top h1 {
	 position: relative;
	 color: white;
	 font-weight: 600;
}
 .CubePlayer .player_inner__top .t_left {
	 width: 25%;
	 float: left;
}
 .CubePlayer .player_inner__top .t_mid {
	 float: left;
	 text-align: center;
	 width: 50%;
}
 .CubePlayer .player_inner__top .t_mid h1 {
	 margin: 0;
	 font-size: 12px;
}
 .CubePlayer .player_inner__top .t_right {
	 float: right;
	 width: 25%;
	 text-align: right;
}
 .CubePlayer .player_inner__middle {
	 position: relative;
	 height: 380px;
	 background: #40555f;
	 box-shadow: 0px 0px 110px #181f23 inset;
}
 .CubePlayer .player_inner__middle .empty {
	 display: none;
}
 .CubePlayer .player_inner__middle input {
	 -webkit-appearance: none;
	 -moz-appearance: none;
	 background: transparent !important;
	 outline: none;
	 transition: all 0.3s;
	 width: 60px !important;
	 height: 50px !important;
	 line-height: 16px;
}
 .CubePlayer .player_inner__middle input:hover {
	 background: #000 61 !important;
}
 .CubePlayer .player_inner__middle input:hover:after {
	 color: #44f5b7 !important;
}
 .CubePlayer .player_inner__middle input:nth-of-type(1) {
	 width: 130px !important;
	 top: 40px;
	 height: 320px !important;
}
 .CubePlayer .player_inner__middle input:nth-of-type(1):hover {
	 background: none !important;
}
 .CubePlayer .player_inner__middle input:nth-of-type(1)::after {
	 font-family: 'Open Sans', sans-serif, 'fontawesome';
	 content: '\f001 Now Playing';
	 display: block;
	 left: 0 !important;
	 color: white;
	 font-size: 13px;
	 cursor: pointer;
}
 .CubePlayer .player_inner__middle input:nth-of-type(2) {
	 display: none;
}
 .CubePlayer .player_inner__middle input:nth-of-type(2)::after {
	 font-family: 'fontawesome';
	 content: '\f001';
	 display: block;
	 color: white;
	 font-size: 18px;
	 cursor: pointer;
}
 .CubePlayer .player_inner__middle input:nth-of-type(3) {
	 BORDER-TOP-LEFT-RADIUS: 50PX;
	 border-bottom-left-radius: 50px;
}
 .CubePlayer .player_inner__middle input:nth-of-type(3)::after {
	 font-family: 'fontawesome';
	 content: '\f028';
	 display: block;
	 color: white;
	 font-size: 18px;
	 cursor: pointer;
}
 .CubePlayer .player_inner__middle input:nth-of-type(3):checked {
	 opacity: 0;
	 right: -50px;
	 box-shadow: 0px 0px 0px 100px #000 61;
	 pointer-events: none;
}
 .CubePlayer .player_inner__middle input:nth-of-type(3):checked + input + input + div > .cube_inner > .cube_inner__right .MuiSvgIcon-root {
	 opacity: 1;
	 top: 8px;
	 transition: all 0.3s 0.3s;
}
 .CubePlayer .player_inner__middle input:nth-of-type(3):checked + input + input + div + div + div + div {
	 display: block;
	 box-shadow: 60px 10px 50px -17px #303f45 inset;
}
 .CubePlayer .player_inner__middle input:nth-of-type(3):checked + input + input + div + div + div + div + div {
	 display: block;
	 box-shadow: -60px 10px 50px -17px #303f45 inset;
}
 .CubePlayer .player_inner__middle input:nth-of-type(4) {
	 BORDER-TOP-right-RADIUS: 50PX;
	 border-bottom-right-radius: 50px;
}
 .CubePlayer .player_inner__middle input:nth-of-type(4)::after {
	 font-family: 'fontawesome';
	 content: '\f1de';
	 display: block;
	 color: white;
	 font-size: 18px;
	 cursor: pointer;
	 left: 4px;
}
 .CubePlayer .player_inner__middle input:nth-of-type(4):checked {
	 opacity: 0;
	 left: -50px;
	 pointer-events: none;
	 box-shadow: 0px 0px 0px 100px #000 61;
}
