	 display: block;
	 box-shadow: -60px 10px 50px -17px #303f45 inset;
}
 .CubePlayer .player_inner__middle .trigger--1, .CubePlayer .player_inner__middle .trigger--2, .CubePlayer .player_inner__middle .trigger--3, .CubePlayer .player_inner__middle .trigger--4 {
	 background: black;
	 width: 20px;
	 cursor: pointer;
	 height: 400px;
	 position: absolute;
	 z-index: 2;
	 margin: auto;
	 text-align: center;
}
 .CubePlayer .player_inner__middle .trigger--1::after, .CubePlayer .player_inner__middle .trigger--2::after, .CubePlayer .player_inner__middle .trigger--3::after, .CubePlayer .player_inner__middle .trigger--4::after {
	 position: relative;
	 top: 18px;
	 left: -6px;
	 transition: all 0.3s;
}
 .CubePlayer .player_inner__middle .trigger--1 {
	 left: 0px;
	 right: auto;
	 top: 0;
	 bottom: 0;
	 width: 20px;
	 height: 40px;
	 border: dotted 4px lime;
}
 .CubePlayer .player_inner__middle .trigger--1:checked + input + .cube {
	 transform: translateY(-50%) rotateX(0) rotateY(30deg);
	 transform-origin: 200px 0px -200px;
	 transition: transform 300ms 200ms, transform-origin 0s;
}
 .CubePlayer .player_inner__middle .trigger--2 {
	 left: auto;
	 right: 0px;
	 top: 0;
	 bottom: 0;
	 width: 20px;
	 height: 40px;
	 border: dotted 4px yellow;
}
 .CubePlayer .player_inner__middle .trigger--2:checked + input +input + .cube {
	 transform: translateY(-50%) rotateX(0) rotateY(-30deg);
	 transform-origin: 200px 0px -200px;
	 transition: transform 300ms 200ms, transform-origin 0s;
}
 .CubePlayer .player_inner__middle .trigger--3 {
	 left: 0;
	 right: 0;
	 top: 0px;
	 bottom: auto;
	 width: 40px;
	 height: 20px;
}
 .CubePlayer .player_inner__middle .trigger--3:checked + input +input +input + .cube {
	 transform: translateY(-50%) rotateX(0) rotateY(0);
	 transform-origin: 200px 200px -200px;
	 transition: transform 300ms 200ms, transform-origin 0s;
}
 .CubePlayer .player_inner__middle .trigger--4 {
	 left: 0;
	 right: 0;
	 top: 0px;
	 bottom: auto;
	 width: 40px;
	 height: 20px;
}
 .CubePlayer .player_inner__middle .trigger--4:checked + input +input +input +input + .cube {
